.interactive
  overflow: hidden
  border-top: 1.5px solid $gray2
  &__header
    @include btn()
    display: flex
    justify-content: space-between
    align-items: center
    width: 100%
    padding: 1.6rem 0
  &__title
    @include titleInteractive()
    letter-spacing: 0.7px
    @media ($tablet)
      font-size: 1.4rem
    @media ($laptop)
      font-size: 1.5rem
  &__tech-vertical
    width: 2px
    height: 12px
    border-radius: 2px
    background-color: $gray2
  &__tech-horizontal
    width: 12px
    height: 2px
    border-radius: 2px
    background-color: $gray2
    transform: translate(-5px,-7.5px)
  &__content
    height: 0
    border-bottom: 1.5px solid $gray2
  &__desc
    @include textBlock()
  &__link
    @include btn()
    @include textBlock()
    display: inline-block
    color: $blue2
    font-weight: 400
    margin: 1.6rem 0
    &::after
      content: ''
      background-image: url(https://help.apple.com/macos/catalina/whats-new/global/0008_outlink-arrow-blue.svg)
      display: inline-block
      width: 1rem
      height: 1rem
      margin-left: 0.5rem
