// Появление ОПИСАНИЯ Стартовой страницы
.descripStart
	animation-name: descripStart
	animation-duration: 4.5s

// Появление Стартовой страницы
.slide-left
	animation: slide-left 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both

// Скрытие Стартовой страницы
.startOpacity
	animation-name: hideStartPage
	animation-duration: 7s


// Появление основной темы
.showPages
	animation-name: showPage
	animation-duration: 5s
.showHeader
	animation-name: show_Header
	animation-duration: 1s


// Появление основновного меню
.showMenu
	animation: showMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both
.hideMenu
	animation: hideMenu 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both
.opacityShow
	animation-name: opacityShow
	animation-duration: 0.3s
.rotate-90-ccw
	animation: rotate-90-ccw 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both
.rotate-anti-90
	animation: rotate-anti-90 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both


// Скрыть/показать содержание блока Интерактив
.showInteractive
	animation: showHideInteractive 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both
.hideInteractive
	animation: hideInteractive 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) backwards
.toBlackText
	animation-name: toBlackText
	animation-duration: 1s
.toWhiteText22
	opacity: 0

// Карточки JS
.slide-left
	animation: slide-left 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both


// Keyframes


// Появление ОПИСАНИЯ Стартовой страницы
@keyframes descripStart
	0%
		opacity: 0
	60%
		opacity: 0
	100%
		opacity: 1

// Появление Стартовой страницы
@keyframes slide-left
	0%
		transform: translateX(150%)
	60%
		transform: translateX(150%)
	// transform: translateX(27vw)
	100%
		transform: translateX(0)


// Скрытие Стартовой страницы
@keyframes hideStartPage
	0%
		opacity: 0
	30%
		opacity: 1
	70%
		opacity: 1
	90%
		opacity: 0

// Появление основной темы
@keyframes showPage
	0%
		opacity: 0
	90%
		opacity: 0.3
	100%
		opacity: 1
@keyframes show_Header
	0%
		opacity: 0
	90%
		opacity: 0.3
	100%
		opacity: 1

// Всплытие Меню
@keyframes showMenu
	0%
		transform: scaleY(0.4)
		transform-origin: 100% 0%
	100%
		transform: scaleY(1)
		transform-origin: 100% 0%
@keyframes hideMenu
	0%
		transform: scaleY(1)
		transform-origin: 100% 0%
	100%
		transform: scaleY(0)
		transform-origin: 100% 0%
@keyframes opacityShow
	0%
		opacity: 0
	70%
		opacity: 0
	100%
		opacity: 1


// Кнопка-крестик
@keyframes rotate-90-ccw
	0%
		transform: rotate(0)
	100%
		transform: rotate(-45deg) translate(2.5px, -2.5px)

@keyframes rotate-anti-90
	0%
		transform: rotate(-45deg) translate(2.5px, -2.5px)
	100%
		transform: rotate(0)

// Скрыть/показать содержание блока Интерактив
@keyframes showHideInteractive
	0%
		transform: scaleY(0.4)
		transform-origin: 100% 0%
	100%
		transform: scaleY(1)
		transform-origin: 100% 0%
@keyframes hideInteractive
	0%
		transform: scaleY(1)
		transform-origin: 100% 0%
	100%
		transform: scaleY(0.3)
		transform-origin: 100% 0%
@keyframes toBlackText
	0%
		opacity: 0
	20%
		opacity: 0
	100%
		opacity: 1

// Карточки JS
@keyframes slide-left
	0%
		transform: translateX(650px)

	100%
		transform: translateX(0)