.start
  background-color: $background2
  height: 100vh
  &__content
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    height: 100vh
    @media ($laptop)
      flex-direction: row
  &__avatar
    opacity: 0
    display: flex
    justify-content: center
    align-items: center
    width: 20rem
    height: 20rem
    background: linear-gradient($white, #c8c5d2)
    border-radius: 50%
    box-shadow: 0 6px 9px #98989d
    @media ($laptop)
      width: 23rem
      height: 23rem
      margin-right: 1rem
    @media ($desktop)
      margin-right: 4rem
    @media ($projector)
      margin-right: 5rem

  &__img
    width: 17rem
    height: 17rem
    background-color: $gray1
    border-radius: 50%
    object-fit: cover
    object-position: center
    @media ($laptop)
      width: 20rem
      height: 20rem
  &__txt
    text-align: center
    margin-top: 5rem
    @media ($phone)
      margin-top: 3rem
    @media ($tablet)
      margin-top: 5rem
    @media ($laptop)
      text-align: left
      margin-top: 0
  &__title
    @include h1()
    line-height: 2rem
    @media ($tablet)
      line-height: 3.5rem
  &__description
    @include h2()
    line-height: 4rem
    @media ($tablet)
      line-height: 6rem
