// LOGO
.logo
  text-decoration: none
  font-size: 2rem
  font-weight: 700
  color: #00e

// HIDE-MENU
// Кнопка Раскрыть/Скрыть навбвр
.hide-menu
  display: inline-block
  cursor: pointer
  border-radius: 10px
  padding: 0.6rem

.bar1, .bar2, .bar3
  @include hamburger()

.change .bar1
  -webkit-transform: rotate(-45deg) translate(-6px,6px)
  transform: rotate(-45deg) translate(-6px,6px)

.change .bar2
  opacity: 0

.change .bar3
  -webkit-transform: rotate(45deg) translate(-4px, -6px)
  transform: rotate(45deg) translate(-4px, -6px)

// NAVBAR
.navbar
  padding-left: 1.5rem
  @media ($projector)
    padding-left: 0
  &__items
    opacity: 0
    display: flex
    flex-direction: column
    justify-content: flex-start
    @media ($phone)
      flex-direction: row
  &__item
    margin-left: 1.5rem
    margin-bottom: 2rem
    @media ($phone)
      margin-left: 3rem
      margin-right: 0
    //&:nth-child(1)
    //  margin-left: 0
    &:nth-last-child(1)
      margin-bottom: 0
  &__link
    font-weight: 400
    font-size: 1.4rem
    text-decoration: none
    text-transform: capitalize
    &:hover
      text-decoration: underline


// Control Buttons
.btn
  display: inline-block
  text-align: center
  border-radius: 50%
  border: 0
  text-decoration: none
  cursor: pointer
  user-select: none
  outline: none

.arr
  display: inline-block
  border: solid $gray1
  border-width: 0 1.5px 1.5px 0
  padding: 4px

.arrowLeft
  transform: rotate(135deg)

.arrowRight
  transform: rotate(-45deg)
