header
  position: relative

.pre-menu
  position: absolute
  bottom: 0px
  top: 0.9rem
  right: 3rem
  z-index: 999
  @media ($tablet)
    right: 7rem
  // @media ($projector)
  //   right: 17rem

.header
  display: none
  height: 6rem
  background-color: $background2
  @media ($tablet)
    background: linear-gradient(to left,$background1 60%,$background2 40%)
  &__nav
    display: none
    height: 16rem
    align-items: center
    @media ($phone)
      height: 6rem
      align-items: flex-end
    @media ($projector)
      padding: 0
