//  Переменные для nextSlidePresentation
.nextSlidePresentation__icon-js
  background-color: #f3dd00
.nextSlidePresentation__iconTxt-js
  color: #000000

.nextSlidePresentation__icon-ra
  background-color: #53c0dd
.nextSlidePresentation__iconTxt-ra
  color: $white

.nextSlidePresentation__icon-an
  background-color: #de0031
.nextSlidePresentation__iconTxt-an
  color: $white
  padding-left: 3.2rem !important

.nextSlidePresentation__icon-wp
  background-color: $cyan2
.nextSlidePresentation__iconTxt-wp
  color: $white
  padding-left: 1.5rem !important

.nextSlidePresentation__icon-ml
  background-color: $red3
.nextSlidePresentation__iconTxt-ml
  color: $white
  padding-left: 1.1rem !important

.nextSlidePresentation__mt260
  margin-top: 2.6rem

.nextSlidePresentation__mt650
  margin-top: 6.5rem

// Основные стили
.nextSlidePresentation
  &__csm
    // стили @cards лежат в миксинах
    @include cards()
  &__devOps
    @include cards()
  &__card
    @include cards()
  &__img
    position: absolute
    top: 0
    left: 0
    height: 100%
    width: auto
    opacity: 0.5
    transition: opacity 1s
  &__desc
    position: relative
    height: 100%
    background: radial-gradient(circle at left,rgba(0,0,0,.6) 0,#000 25%)
    padding: 2rem
    z-index: 50
  &__main
    height: 29.5rem
  &__title
    @include titleCard()
  &__subtitle
    @include subTitleCard()
  &__lable
    display: flex
    align-items: center
    margin: 1rem 0 1.5rem
  &__icon
    width: 7.8rem
    height: 7.8rem
    border-radius: 50%
    margin-right: 1.5rem
  &__iconTxt
    font-size: 4rem
    font-weight: 600
    padding-left: 20px
    padding-top: 30px
  &__position
    @include positionTitleCard()
  &__txt
    @include descCard()
  &__statistic
    @include statisticCard()
    margin-top: 1rem
  &__link
    @include btn()
    display: inline-block
    font-size: 14px
    color: $white
    background-color: $btnCard
    border-radius: 40px
    text-align: center
    vertical-align: middle
    line-height: 1.25
    padding: 10px 20px
    white-space: nowrap
    &:hover
      background-color: $btnCardHover
  &__link-lightColor
    background-color: $btnCard2
    margin-left: 1rem
    &:hover
      background-color: $btnCardHover2
