.pages
  @include section()
  margin-top: -10000px
  &__mobile
    overflow: hidden
    height: auto
    @media ($tablet)
      height: 0
  &__desktop
    overflow: hidden
    height: 0
    @media ($tablet)
      height: auto
  &__info
    @include infoBlock()
    //overflow: hidden
    // @media ($tablet)
    //   height: 18.5rem
    // @media ($laptop)
    //   height: 21.5rem
    // @media ($desktop)
    //   height: 23.5rem
    // @media ($projector)
    //   height: 29.5rem
  &__descMobile
    @include description()
    display: block !important
  &__desc
    @include description()
  &__title
    @include title()
  &__subtitle
    @include subtitle()
  &__txt
    @include textBlock()
    margin-bottom: 2.5rem
  &__demo
    display: none
    margin: 0 auto
    padding-top: 7rem
    width: auto
    @media ($tablet)
      display: block
      padding-top: 4rem
    @media ($laptop)
      padding-top: 7rem
  &__footer
    display: none
    width: 100%
    height: 6rem
    text-align: center
    vertical-align: middle
    background: $background1
    padding: 0 1rem
    @media ($tablet)
      display: block
  &__footer-txt
    padding-top: 1.8rem
    color: $gray1
    font-weight: 300
    font-size: 1.2rem
    line-height: 1.5
    letter-spacing: 0.5px
  &__navigation
    justify-content: space-between
    padding: 0 2.1rem
    margin-bottom: 2rem
  &__btn
    width: 4.4rem
    height: 4.4rem
    &:hover
      background-color: $background3
  &__hidden
    overflow: hidden
    border-radius: 10px
  &__link
    @include link()
  &__img
    width: 100%
