// FONTS
@mixin h1($color: $mainColor)
  color: $color
  font-family: $mainFont
  font-size: 2rem
  font-weight: 500
  letter-spacing: 0.3px
  @media ($tablet)
    font-size: 3rem

@mixin h2($color: $mainColor)
  color: $color
  font-family: $mainFont
  font-size: 3rem
  font-weight: 500
  @media ($tablet)
    font-size: 5.3rem
    letter-spacing: 1.3px

@mixin h4($color: $gray3)
  color: $color
  font-family: $mainFont
  font-size: 1.6rem
  font-weight: 400
  text-transform: capitalize
  @media ($tablet)
    font-size: 1.8rem
    letter-spacing: 1.3px

@mixin title()
  font-size: 2.3rem
  font-weight: 500
  text-transform: uppercase
  letter-spacing: 0.69px
  line-height: normal
  margin-top: 7rem
  @media ($phone)
    margin-top: 0
  @media ($laptop)
    font-size: 4rem
    font-weight: 500
    letter-spacing: -1.2px
    line-height: normal
    margin-top: 0
    margin-left: 0

@mixin subtitle()
  color: $mainColor
  font-size: 2.3rem
  font-weight: 500
  letter-spacing: 0.69px
  line-height: 1.1
  margin-bottom: 0.8rem
  @media ($laptop)
    color: $mainColor
    font-size: 4rem
    font-weight: 500
    letter-spacing: -1.2px
    line-height: 1.1
    margin-bottom: 2.5rem

@mixin textBlock()
  color: $mainColor
  font-size: 1.5rem
  font-weight: 300
  letter-spacing: normal
  line-height: 1.5
  @media ($tablet)
    font-size: 1.2rem
  @media ($laptop)
    font-size: 1.5rem

@mixin titleInteractive()
  font-size: 1.5rem
  letter-spacing: normal
  font-weight: 700

@mixin titleCard()
  font-size: 2.7rem
  line-height: 40px
  color: $titleCard
  &:hover
    color: $titleCardHover

@mixin subTitleCard()
  font-size: 1.8rem
  line-height: 27px
  color: $subTitleCard

@mixin positionTitleCard()
  font-size: 1.6rem
  color: $positionTitleCard
  margin-top: 0.5rem

@mixin descCard()
  font-size: 1.4rem
  line-height: 21px
  color: $subTitleCard

@mixin statisticCard()
  font-size: 1.4rem
  line-height: 21px
  color: $positionTitleCard

// BTN Раскрыть/Скрыть меню
@mixin hamburger()
  width: 3rem
  height: 0.3rem
  background-color: $violet
  margin: 0.5rem 0
  transition: 0.4s

// SECTIONS
@mixin section()
  display: flex
  flex-direction: column
  flex-wrap: wrap
  @media ($tablet)
    flex-direction: row

// BLOCKS слева
@mixin infoBlock()
  display: flex
  flex-direction: column
  justify-content: flex-start
  height: 100%
  background-color: $background2
  @media ($tablet)
    width: calc(100vw - 60%)
    max-width: 100vw

@mixin description()
  max-width: 41rem
  margin: 0 auto
  padding: 3.6rem 1.5rem 0
  @media ($phone)
    max-width: 45rem
    padding: 4rem 0 0
  @media ($tablet)
    display: none
    max-width: 41rem
    padding: .6rem 3.5rem 0
  @media ($laptop)
    padding: 6.6rem 1.5rem 0
  @media ($projector)
    padding: 8.6rem 1.5rem 0

// BTNS
@mixin btn()
  cursor: pointer
  border: 0
  text-decoration: none
  user-select: none
  padding: 0
  background-color: transparent

@mixin link()
  text-decoration: none
  user-select: none

// Images
@mixin img()
  background-color: $background1
  object-fit: fill
  object-position: top

// Crds
@mixin cards()
  position: relative
  width: 40rem
  height: 22rem
  background-color: #222222
  overflow: hidden
  box-sizing: border-box
  padding-left: 10rem
  &:hover
    .nextSlidePresentation__img
      opacity: 1
  @media ($laptop)
  @media ($desktop)
  @media ($projector)
    width: 55rem
    height: 38rem
