html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video
  margin: 0
  padding: 0
  border: 0
  font-size: 100%
  font: inherit
  vertical-align: baseline

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section
  display: block

body
  line-height: 1

ol,
ul
  list-style: none

blockquote,
q
  quotes: none

blockquote:before,
blockquote:after,
q:before,
q:after
  content: ""
  content: none

table
  border-collapse: collapse
  border-spacing: 0

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration
  -webkit-appearance: none
  -moz-appearance: none

input[type="search"]
  -webkit-appearance: none
  -moz-appearance: none
  -webkit-box-sizing: content-box
  -moz-box-sizing: content-box
  box-sizing: content-box

textarea
  overflow: auto
  vertical-align: top
  resize: vertical

audio,
canvas,
video
  display: inline-block
  zoom: 1
  max-width: 100%

figure
  margin: 0

form
  margin: 0

button,
input
  line-height: normal

button,
select
  text-transform: none

button::-moz-focus-inner,
input::-moz-focus-inner
  border: 0
  padding: 0

table
  border-collapse: collapse
  border-spacing: 0

html,
button,
input,
select,
textarea
  color: #222

::-moz-selection
  background: #b3d4fc
  text-shadow: none

::selection
  background: #b3d4fc
  text-shadow: none

img
  vertical-align: middle

fieldset
  border: 0
  margin: 0
  padding: 0

textarea
  resize: vertical
